import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import { Col } from "react-bootstrap";
import CursoCard from "./curso-card";

export default function CursosBlock() {
  const data = useStaticQuery(pageQuery);
  const categorias = data.allWpProductCategory.edges;

  return (
    <div className="curso-selection">
      {categorias.map(cat => (
        <div className="curso-selection-category" key={cat.node.id}>
          <h1>{cat.node.name}</h1>
          {cat.node.products.nodes.map(prod => (
            <CursoCard curso={prod} key={prod.id}></CursoCard>
          ))}
        </div>
      ))}
    </div>
  );
}

const pageQuery = graphql`
  query CursosBlock {
    allWpProductCategory(filter: { name: { ne: "Sin categorizar" } }) {
      edges {
        node {
          id
          name
          products {
            nodes {
              id
              name
              image {
                id
                sourceUrl
                localFile {
                  id
                  childImageSharp {
                    id
                    fluid {
                      ...GatsbyImageSharpFluid_noBase64
                    }
                  }
                }
              }
              acfCursos {
                duracion
                fechaDeInicio
                fechaHora
                fieldGroupName
                metodologia
                modalidadDeClases
                orientacion
                precioFormasDePago
                profesor
                temario
                fotoProfesor {
                  id
                  sourceUrl
                  localFile {
                    id
                    childImageSharp {
                      id
                      fluid {
                        ...GatsbyImageSharpFluid_noBase64
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
`;
