import React from "react";
import { graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";
import { Col } from "react-bootstrap";

export default function CursoCard({ curso }) {
  const {
    duracion,
    fechaDeInicio,
    metodologia,
    modalidadDeClases,
    orientacion,
    precioFormasDePago,
    profesor,
    temario,
  } = curso.acfCursos;

  const fotoProfesor = curso.acfCursos.fotoProfesor
    ? curso.acfCursos.fotoProfesor.localFile.childImageSharp.fluid
    : null;
  const imagenCurso = curso.image
    ? curso.image.localFile.childImageSharp.fluid
    : null;
  return (
    <div className="curso-card">
      <div className="header">
        {fotoProfesor ? <Img fluid={fotoProfesor}></Img> : null}
        {imagenCurso ? <Img fluid={imagenCurso}></Img> : null}
      </div>
      <div className="title">
        <h3>{curso.name}</h3>
      </div>
      <div className="body">
        <div>{profesor}</div>
        <div>{modalidadDeClases}</div>
        <div>{fechaDeInicio}</div>
        <div>{duracion}</div>
      </div>
    </div>
  );
}
